import React from "react";
import { useEffect, useState } from "react";
import {Link, useHistory} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import{getGenres,postVideoGame,getPlatforms, getAllGames} from '../../redux/actions/index';
import styles from './CreatedVideoGames.module.css'

const validatedForm = (input) => {
    let error = {};
    if(!input.name){
        error.name = 'name is required';
    } else if (input.name.length < 3){
        error.name = 'must have at least 3 characters'
    }
    
    if(!input.description){
        error.description = 'description required';
    } else if (/[A-Za-z0-9.,;:!?()"'%-]+/.test(input.description)&& 
    input.description.length < 20){
        error.description = 'must be at least 20 characters'
    };

    if(!input.rating){
        error.rating = 'rating from 1 to 5 is required'
    } else if (input.rating < 1 || input.rating > 5){
        error.rating = 'the rating must be between 1 and 5'
    };

    
    if(!input.released){
        error.released = 'released date required'
    } else if (/^(0[1-9]|1[0-2])\/(0[1-9]|1\d|2\d|3[01])\/(19|20)\d{2}$/.test(input.released)){
        error.released = 'must be a valid date'
    };

    
    if(!input.image){
    } else if (!/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#()?&//=]*)/.test(input.image)){
        error.image = 'the image must be a valid url'
    };

    return error;

};


export default function CreateVideoGame (){
    const dispatch = useDispatch();
    const videogames = useSelector((state)=> state.videogames)
    const genres = useSelector((state)=> state.genres)
    const platforms = useSelector((state)=> state.platforms)
    const history= useHistory()
    const [errors, setErrors] = useState({})
    const [state, setState]= useState({
        name:"",
        description:"",
        image:"",
        released:"",
        platforms:[],
        rating:"",
        genres:[]
    });
    useEffect(()=>{
        dispatch(getAllGames())
        dispatch (getGenres())
        dispatch(getPlatforms())
    },[dispatch])

    const handleChange =(e) =>{
        setState({
            ...state,
            [e.target.name]: e.target.value
        })

        setErrors(
            validatedForm({
                ...state,
                [e.target.name]: e.target.value
            })
        )
    }
    const handleSelectGenre = (e)=> {
        if(Object.values(state.genres).includes(e.target.value)){
            alert('the genre has already been selected')
        } else {
            setState({
                ...state,
                genres:[...state.genres, e.target.value]
            })
        }
    };
    const handleSelectPlatforms =(e)=>{
        if(Object.values(state.platforms).includes(e.target.value)){
            alert('the platforms has already been selected')
        } else {
            setState({
                ...state,
                platforms:[...state.platforms, e.target.value]
            })
        }
    };
    const handleSubmitForm =(e)=>{
        if(videogames.find((videogame)=> videogame.name.toLowerCase()=== state.name.toLowerCase())){
            alert('the game already exists')
            e.prevent.default();
        } else {
            e.prevent.default();
            if(state.platforms.length ===0){
                return alert('at least 1 platform is required')
            }else if(state.platforms.length > 4){
                return alert('maximum 4 platforms can be added')
            }else if(state.genres.length ===0){
            return alert('at least 1 genre is required')
        }else if(state.genres.length > 4){
            return alert('maximum 4 genres can be added')
        }
    dispatch(postVideoGame(state))
    alert('videogame created successfully!')
    setState({
        name:"",
        description:"",
        image:"",
        released:"",
        platforms:[],
        rating:"",
        genres:[]
    })
    history.push('/home')
    }
};
return (
    <div>
        <div className={styles.containerForm}>
            <Link to='/home'>
                <button classname={styles.buttonBack}>Back</button>
            </Link>
            <div classname={styles.containertitle}>
                <h1>Create Videogame</h1>
                </div>
                <form className={styles.containerFormInside}
                onSubmit={(e)=> handleSubmitForm(e)}>
                    <div> 
                        <h1 className={styles.textForm}>Name:</h1>
                        <input className={styles.inputForm}
                        onChange={(e)=> handleChange(e)}
                        type='text'
                        value={state.name}
                        name ='name'
                        required={true}></input>
                        {errors.name && <p className={styles.errorText}>{errors.name}</p>}
                        </div>
                        <div>
                            <h5 className ={styles.textForm}>Description:</h5>
                            <textarea
                            className={styles.inputFormDescription}
                            onchange={(e)=> handleChange(e)}
                            type='text'
                            value= {state.description}
                            name='description'></textarea>
                            {errors.description && (<p className={styles.errorText}>{errors.description}</p>)}
                            </div>
                            <div> 
                                <h5 className={styles.textForm}>Genres:</h5>
                                <select className={styles.selectForm}
                                onChange={(e)=> handleSelectGenre(e)}>
                                    <option hidden>Select Genre</option>
                                    {genres.map((e)=>{
                                    return (
                                        <option key ={e.id} value = {e.name}>
                                            {e.name}
                                            </option>
                                    
                                    )
                                    })}
                                    </select>
                                    <div className={styles.containerButtonForm}>
                                        {state.genres.map((e)=>{
                                            return (
                                                <div key ={e}>
                                                    <button className={styles.buttonForm}
                                                    // onClick={()=> handleDeleteGenres(e)}
                                                    >{e}</button>
                                                    </div>
                                            )
                                        })};
                                        </div>
                                        </div>
                                        {errors.genres && <p className={styles.errorText}>{errors.genres}</p>}
                                        <div>
                                            <h5 className={styles.textForm}>Plataforms:</h5>
                                            <select
                                            className={styles.selectForm}
                                            onChange={(e)=> handleSelectPlatforms(e)}>
                                                <option hidden> Select Plataform</option>
                                                {platforms.map((e)=>{
                                                    return (
                                                        <option key ={e.id} value= {e.name}>
                                                            {e.name}
                                                        </option>
                                                    )
                                                })}
                                                </select>
                                                </div>
                                                <div className={styles.containerButtonForm}>
                                                    {state.platforms ? state.platforms.map((e)=>{
                                                        return (
                                                            <div key={e}>
                                                                <button 
                                                                className={styles.buttonForm}
                                                                //onClick={()=> handleDeletePlatforms(e)}
                                                        >{e}</button>
                                                        </div>)
                                                    }):null}
                                                    </div>
                                                    {errors.platform && (
                                                        <p className={styles.errorText}>{errors.platforms}</p>
                                                    )}
                                                    <div>
                                                        <h5 className={styles.textForm}>Release date:</h5>
                                                        <input className={styles.inputForm}
                                                        onChange={(e)=> handleChange(e)}
                                                        type= 'date'
                                                        value = {state.released}
                                                        name='released'
                                                        ></input>
                                                        {errors.released && (
                                                            <p className={styles.errorText}>{errors.released}</p>
                                                        )}
                                                        </div>
                                                        <div>
                                                            <h3 className={styles.textForm}>Image:</h3>
                                                            <input className={styles.inputForm}
                                                            onChange={(e)=> handleChange(e)}
                                                            type='text'
                                                            value={state.image}
                                                            name='image'
                                                            ></input>
                                                            <p className={styles.errorText}>{errors.image}</p>
                                                            </div>
                                                            <div> 
                                                                <h3 className={styles.textForm}>Rating:</h3>
                                                                <input className={styles.inputForm}
                                                                onChange={(e)=> handleChange(e)}
                                                                type='number'
                                                                step={0.1}
                                                                value={state.rating}
                                                                name= 'rating'
                                                                ></input>
                                                                {errors.rating && (
                                                                    <p className={styles.errorText}>{errors.rating}</p>
                                                                )}
                                                                </div>
                                                                <div>
                                                                    <button 
                                                                    className={styles.button}
                                                                    disabled= {Object.entries(errors).length===0? false: true}>
                                                                        Create Videogame
                                                                    </button>
                                                                    </div>
                                                                    </form>
                                                                    </div>
                                                                    </div>
)
};