import axios from 'axios';

export const GetAllGames='Get_All_Games';
export const OrderByName = 'Order_By_Name';
export const GetGenres='Get_Genres';
export const GetPlatforms='Get_Platforms';
export const filterByCreated='Filter_By_Created';
export const filterByGenre='Filter_By_Genre';
export const OrderByRating='Order_By_Rating';
export const searchByName='Search_By_Name';
export const getDetails ='Get_Details';
export const PostVideoGame='Post_VideoGame';


export function getAllGames(){
    return async function(dispatch){
        const response = await axios.get('http://localhost:3001/videogames');
        return dispatch({
            type: GetAllGames,
            payload: response.data
        })
    }
};

export function getGenres(){
    return async function(dispatch){
        const response = await axios.get('http://localhost:3001/genres');
        return dispatch({
            type: GetGenres,
            payload: response.data
        })
    }
};

export function getPlatforms(){
    return async function(dispatch){
        const response = await axios.get('http://localhost:3001/platforms');
        return dispatch({
            type: GetPlatforms,
            payload: response.data
        })
    }
};

export function FilterByCreated(payload){
    return {
        type: filterByCreated,
        payload
    }
};

export function FilterByGenre(payload){
    return {
        type: filterByGenre,
        payload
    }
};

export function orderByRating(payload){
        return{
            type: OrderByRating,
            payload
    }
};

export function SearchVideoGame(name){
    return async function(dispatch){
        try {
        const response = await axios.get(`http://localhost:3001/videogames?name=` + name)
        return dispatch({
            type: searchByName,
            payload: response.data
        })
    } catch (error) {
        console.log(error)
    }
    }
};

export function GetDetails(id){
    return async function(dispatch){
        try{
            const response = await axios.get(`http://localhost:3001/videogames/${id}`);
        return dispatch({
            type: getDetails,
            payload: response.data
        })
    }
    catch(error){
        console.log(error)
    }
    }
};

export function postVideoGame(payload){
    return async function(dispatch){
        try{
            const response = await axios.post(`http://localhost:3001/videogames`, payload);
        return dispatch({
            type: PostVideoGame,
            payload: response
        })
    } catch(error){
        console.log(error)
    }
    }
};

export function orderByName(payload){
    return{
        type: OrderByName,
        payload
    }
};