import { useState, useEffect, React } from "react";
import {Link, useParams} from 'react-router-dom';
import { useSelector, useDispatch } from "react-redux";
import { GetDetails } from "../../redux/actions";
import style from './VideoGameDetails.module.css';
import Loader from '../Loader/Loader';
export default function VideoGameDetail(){
    const dispatch = useDispatch();
    const videogame= useSelector((state)=> state.detailGame);
    const {id} = useParams();
    
    const [loader, setLoader]= useState(false)
    useEffect(()=>{
        setLoader(true)
        dispatch(GetDetails(id));
        setTimeout(()=>{
            setLoader(false)
        },1000)
    },[dispatch, id]);

    return (
        <div>
            {loader ? <Loader/> : (
                <div className={style.containerBgDetail}>
                    <div className={style.containerDetail}>
                        <div className={style.containerImg}>
                            <img
                            className={style.imgDetail}
                            src={videogame.image}
                            alt='img' />
                            </div>
                            <div className={style.containerTitle}>
                                <h1 className={style.detail.Title}>{videogame.name}</h1>
                                </div>
                                <div className={style.containerDescription}>
                                    <div className={style.descriptionText}>
                                        Description:
                                        {videogame.description && (
                                            <p>{videogame.description.replace(/<\/?[^>]+(>|$)/g, "")}</p>
                                        )}
                                        </div>
                                        </div>
                                        <div className={style.containerGpr}>
                                            <p className={style.dateText}>
                                                Release year: {videogame.released}
                                            </p>
                                            <p className={style.ratingText}>Rating: {videogame.rating}</p>
                                            <p className={style.platformsText}>
                                                platforms={" "}
                                                {videogame.platforms.map((e, i)=>{
                                                    return i === videogame.patforms.length -1 ? e:e+" - ";
                                                })}
                                                </p>
                                                <p className={style.genresText}>
                                                    Genres:{" "}
                                                    {videogame.createdInDb ?
                                                    videogame.genres.map((e, i)=>{
                                                        return i === videogame.genres.length -1 ? e.name : e.name 
                                                        + " - ";
                                                    })
                                                :videogame.genres && videogame.genres.map((e, i)=>{
                                                    return i === videogame.genres.length -1 ? e:e + " - ";
                                                })}
                                                </p>
                                                <Link to='/home'>
                                                    <button className={style.btnBack}>Back</button>
                                                </Link>
                                                </div>
                                                </div>
                                                </div>

            )}
        </div>
    )};