/* eslint-disable no-fallthrough */
/* eslint-disable no-lone-blocks */
import { GetAllGames,
    OrderByName,
    OrderByRating,
    searchByName,
    getDetails,
    PostVideoGame,
    filterByCreated,
    filterByGenre,
    GetGenres,
    GetPlatforms} from '../actions/index'


const initialState = {
    videogames: [],
    videogameDetail: {},
    genres: [],
    platforms: [],
    AllVideogames: [],
}


export default function rootReducer(state = initialState, action){
    switch(action.type){
        case GetAllGames:{
            return {
                ...state,
                videogames: action.payload,
                AllVideogames: action.payload
            }
};
        case OrderByName:{
        const order = action.payload === 'asc'
        ? state.videogames.sort((a, b) => {
            if(a.name.toLowerCase() > b.name.toLowerCase()) return 1;
            if(a.name.toLowerCase() < b.name.toLowerCase()) return -1;
            return 0;
        })
        : state.videogames.sort((a, b) => {
            if(a.name.toLowerCase() < b.name.toLowerCase()) return 1;
            if(a.name.toLowerCase() > b.name.toLowerCase()) return -1;
            return 0;
        })
        return {
            ...state,
            videogames: order.map((e) => e)
}
};
        case OrderByRating:{
            const SortByRating =action.payload ==='min' ?
            state.videogames.sort((a, b) => {
                if(a.rating> b.rating) return 1;
            if(a.rating< b.rating) return -1;
            return 0;
        })
        : state.videogames.sort((a, b) => {
            if(a.rating< b.rating) return 1;
            if(a.rating> b.rating) return -1;
            return 0;
        })
        return {
            ...state,
            videogames: SortByRating.map((e) => e)
        }
};
        case searchByName:{
            const search = action.payload
            if(Array.isArray(search)){
                return {
                    ...state,
                    videogames: search
                }
            }
        };
        case getDetails:{
            return {
                ...state,
                videogameDetail: action.payload
            }
        };
        case PostVideoGame:{
            return {
                ...state
            }
        };
        case filterByCreated:{
            const AllGamesCreated = state.AllVideogames
            const filterCreated = action.payload === 'created' ?
            AllGamesCreated.filter((e) => e.createdInDb)
            : AllGamesCreated.filter((e) => !e.createdInDb)
            if(filterCreated.length===0){
                alert('No hay juegos creados')
            return {
                ...state,
                videogames: AllGamesCreated
            }
        } else {
            return {
                ...state,
                videogames: filterCreated
            }
        }
    };

        case filterByGenre:{
            const filter = action.payload === 'All' ? state.AllVideogames : state.AllVideogames.filter((e) => e.genres.includes(action.payload))
            return {
                ...state,
                videogames: filter
            }
        };
        
        case GetGenres:{
            return {
                ...state,
                genres: action.payload
            }
        };
        
        case GetPlatforms:{
            return {
                ...state,
                platforms: action.payload
            }
        };
        default:
            return state;
    }
};